*{
  box-sizing: border-box;
  margin: 0;
  line-height: 1;
}

.container {
  display: flex;
  width: 100%;
}
.wrap {
  width: 1140px;
  margin: auto;
}
.wrap, .container {
  height: 100%;
}
.space-between {
  justify-content: space-between;
}
.align-center {
  align-items: center;
}
input, textarea, button {
  border: none;
}
input:focus, textarea:focus, button:focus{
  outline: none;
}
img,a, button {
  display: block;
  cursor: pointer;
}
ul {
  list-style-type: none;
}
a {
  text-decoration: none;
  color: #3CA2E0;
}

.left__block {
  width: 30%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.sidebar {
  width: 100%;
  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: 0 0 20px rgba(60, 162, 224, 0.5);
}
.sideBarBody {
  padding: 5%;
  color: #3CA2E0;
}.sideBarBody span {
   font-weight: 700;
 }
.sideBarBody p {
  font-size: 18px;
  line-height: 36px;
}
.vh-45 {
  height: 45vh;
}
.vh-70 {
  height: 70vh;
}
.sideBar-title {
  width: 100%;
  font-size: 18px;
  background-color: rgba(60, 162, 224, 0.2);
  color: #3CA2E0;
  font-weight: 400;
  border-radius: 10px 10px 0 0;
  padding: 5%;
}
.userCard {
  display: flex;
  align-items: center;
  height: 60px;
  width: 100%;
  border-bottom: 1px solid #FFBE74;
}
.userCard:last-child {
  border: none;
}
.main__block {
  position: relative;
  width: 65%;
  height: 100vh;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 0 20px rgba(60, 162, 224, 0.5);
  padding: 3%;
}
.send__block {
  display: flex;
  justify-content: flex-end;
}
.mt-20 {
  margin-top: 20px;
}
