.postBlock {
    width: 100%;
    margin-top: 20px;
    border-radius: 10px;
    padding: 10px;
    background-color: #F1F1F1;
    border: 1px solid #BBBBBB;
    color: #828282;
}
.postText {
    margin-bottom: 10px;
    margin-left: 30px;
}
.postData {
    display: flex;
    justify-content: space-between;
    font-size: 12px;
    color: #BBBBBB;
}
.userName {
    display: flex;
    align-items: center;
}
.userName img {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    margin-right: 10px;
}