.navbar {
    width: 100%;
    height: 80px;
    background-color: #3CA2E0;
    position: fixed;
    top: 0;
    z-index: 5;
}
.logo {
    font-size: 32px;
    color: #fff;
    height: 70px;
    width: 70px;
    border-radius:50%;
    background-color: #FFBE74;
    display: flex;
    justify-content: center;
    align-items: center;
    line-height: 1;
    box-shadow: 0 0 15px #FFA000;
}