.container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.getBtn {
    width: 200px;
    height: 70px;
    font-size: 26px;
    text-align: center;
    color: #fff;
    background-color: darkred;
    border-radius: 25px;
    margin: 0 auto 30px;
}
.btns_block {
    margin: 50px 0;
    display: flex;
    justify-content: center;
}
.pagesBtn {
    margin: 0 10px;
    padding: 0;
    width: 30px;
    line-height: 30px;
    border-radius: 3px;
    border: 1px solid darkred;
    background-color: #fff;
    color: darkred;
    font-size: 18px;
    text-align: center;
    transition: all 0.3s ease;
}
.pagesBtn:hover {
    background-color: darkred;
    color: #fff;
}
.selectedPage {
    background-color: darkred;
    color: #fff;
}
