
.post__btn {
    padding: 10px 15px;
    font-size: 18px;
    font-weight: 600;
    color: #fff;
    background-color: #3CA2E0;
    border-radius: 4px;
    border: 1px solid #3CA2E0;
    transition: all 0.3s ease;
}
.post__btn:hover {
    color: #3CA2E0;
    background-color: #fff;
}
