.posts__block {
    margin-top: 270px;
}
.post__input {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 200px;
    overflow: hidden;
    padding: 5%;
    border-radius: 10px 10px 0 0;
    border-bottom: 1px solid #3CA2E0;
}
.type__post__block {
    position: absolute;
    width: 94%;
    transform: translateX(-50%);
    left: 50%;
    top: 3%;
}
.type__post__block textarea {
    width: 100%;
    height: 200px;
    overflow: hidden;
    padding: 10px;
    border: 1px solid #3CA2E0;
    border-radius: 10px;

}
