.card {
    width: 30%;
    margin: 0 1.6% 3.2% 1.6%;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0px 0px 20px #BBBBBB;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.avatar {
    margin-top: 20px;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
}
.userName {
    margin-top: 20px;
    color: #3CA2E0;
}
.userStatus {
    margin-top: 5px;
    color: #3CA2E0;
}
.btns_block {
    margin-top: 25px;
    width: 100%;
    display: flex;
    justify-content: center;
    font-weight: bold;
}
.btns_block button {
    font-weight: bold;
    font-size: 16px;
    color: #fff;
    border-radius: 4px;
}
.f_btn {
    margin-right: 20px;
    width: 80px;
    text-align: center;
    background-color: mediumspringgreen;
    padding: 5px;
}
.m_btn {
    background-color: #F1F1F1;
    margin-right: 20px;
    padding: 5px 15px;
}
.h_btn {
    background-color: #3CA2E0;
    padding: 5px;
}
.card_footer {
    width: 100%;
    margin-top: 30px;
    padding: 30px;
    border-top: 1px solid #BBBBBB;
    text-align: center;
}
