.block {
    width: 40%;
    height: 60%;
}
.menu {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
    padding:0px;
}
.link {
    font-size: 18px;
    color: #fff;
    transition: all 0.3s ease;
    padding: 3px;
}
.link:hover {
    color: #3CA2E0;
    background-color: #fff;
    border-radius: 4px;
}
.active {
    color: #3CA2E0;
    background-color: #fff;
    border-radius: 4px;
}