.messege__block {
    max-width: 70%;
    width: auto;
    margin-top: 20px;
    border-radius: 10px;
    padding: 10px;
    background-color: #F1F1F1;
    border: 1px solid #BBBBBB;
    color: #828282;
}
.messege__block:first-child {
    margin-top: 0;
}
.messege__block:nth-child(odd) {
    background-color: #fff;
    margin-left: 30%;
}
.user__messege {
    display: flex;
    align-items: flex-start;
}
.userIcon{
    width: 30px;
    height:30px;
    border-radius: 50%;
    margin-right: 15px;
}
.messegeText {
    margin-bottom: 10px;
    margin-left: 30px
}
.date{
    display: flex;
    justify-content: flex-end;
    font-size: 12px;
    color: #BBBBBB;
}

