.block {
    width: 15%;
    height: 35px;
    border-radius: 5px;
    position: relative;
}
input {
    width: 100%;
    height: 100%;
    border-radius: 5px;
    font-size: 16px;
    padding: 5px;
}
.block img {
    width: 22px;
    height: 22px;
    position: absolute;
    top: 50%;
    right: 10px;
    transform: translateY(-50%);
}