.type__mess_block {
    position: absolute;
    width: 94%;
    transform: translateX(-50%);
    left: 50%;
    bottom: 0;
}
.type__mess_block textarea {
    width: 100%;
    height: 200px;
    overflow: hidden;
    border: 1px solid #3CA2E0;
    border-radius: 10px;
    padding: 10px;
}
.messeges__block {
    display: flex;
    flex-direction: column;
}

